import React, { useState } from 'react';
import { Tabs, Tab, Grid ,CardText, Cell, Card, CardTitle, CardActions, Button, CardMenu, IconButton } from 'react-mdl';

function project (props) {
    const [activeTab, setActiveTab] = useState(0);

    function MainCategory() {
        if(activeTab === 0){
            return(
                <div className="card-display">
                {/* // ---------------------------------------------
                // Project #1
                // --------------------------------------------- */}
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title"><h1>MID STORY</h1></CardTitle>
                    <CardText className="card-text"> 
                        Saya sebut ini adalah Mid Story. Sebuah website (React) yang terinspirasi dari Mid Story.
                        Apa itu Mid Story? Mid Story adalah sebuah ungkapan atau kata-kata inspiratif, puitis yang terinspirasi dari
                        kisah hidup seorang lelaki yang dibesarkan dengan jerih payah orang tuanya. Sebuah kisah 
                        menarik dimulai dari ketika ia memulai sebuah hubungan percintaanya. Semua berawal
                        dari situ. Ingin tahu kelanjutannya? Tunggu saja sampai buku Mid Story release di toko 
                        buku tercinta anda.
                        Sebelumnya saya akan menjelaskan di web ini saya menggunakan library 'react-mdl' dan 'react-router-dom'
                        apabila ada yang tidak tahu 'react-router-dom' itu apa. Bisa dilihat di project saya yang "Login Page".
                        
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
                {/* // ---------------------------------------------
                // Project #2
                // --------------------------------------------- */}
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title1"><h1>LOGIN PAGE</h1></CardTitle>
                    <CardText className="card-text"> 
                        Sebuah webpage yang berisikan Login dan Register, dengan cara menarik data dari API
                        untuk POST (REGISTER) dan untuk GET (LOGIN). Di sini page login dan register menggunakan
                        library 'react-router-dom' disini kita akan mempelajari bagaimana cara penggunaan 'react-router-dom'
                        sebelumnya perlu kita ketahui fungsi 'react-router-dom' ini, yaitu berfungsi sebagai memindahkan dari
                        suatu file javascript ke file javascript lainnya. Contoh pada page ini adalah perpindahan dari Sign-in page
                        ke Sign-Up page. Untuk selebihnya bisa dilihat di github saya. Tinggal klik tombol dibawah ini. Apabila
                        ada yang ingin ditanyakan bisa follow instagram saya atau sosial media lainnya.
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
                </div>
            );
        }
        else if(activeTab === 1){
            return(
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title2"><h1>SuperCeLL</h1></CardTitle>
                    <CardText className="card-text"> 
                    Sebuah website based on Laravel (MVC). Website ini bertujuan untuk memudahkan
                    sebuah Toko HandPhone untuk mengelola barang dagangannya melalui website ini.
                    Jadi admin dapat mengontrol setiap barang yang masuk dan Pembeli pun mudah untuk
                    melihat stock handphone yang ada. Website ini dirancang khusus dengan bantuan
                    phpMyAdmin sebagai databasenya untuk menyimpan data customer dan data items.                        
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
            );
        }
        else if(activeTab === 2){
            return(
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title3"><h1>Create-Insert-SQL</h1></CardTitle>
                    <CardText className="card-text"> 
                    Project dasar cara create dan insert di sql. Project ini simple, hanya berisikan cara-cara create table dan 
                    insert table. Membuat basic SQL.
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
            );
        }
        else if(activeTab === 3){
            return(
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title4"><h1>Library App</h1></CardTitle>
                    <CardText className="card-text"> 
                    Sebuah aplikasi untuk memudahkan peminjaman dan pengelolaan perpustakaan. 
                    Rata-rata aplikasi ini sudah banyak digunakan oleh berbagai macam perpustakaan di
                    sekolah maupun di universitas. Karena sistem yang memudahkan para peminjam dan 
                    pengelola. Membuat aplikasi ini sudah menjadi standarisasi di setiap perpustakaan.
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
            );
        }
        else if(activeTab === 4){
            return(
                <Card className="card-main" shadow={5} >
                    <CardTitle className="card-title5"><h1>Travel-One</h1></CardTitle>
                    <CardText className="card-text"> 
                    Sebuah aplikasi based on Apple/IOS (Swift) didevelop di xcode. Dimana aplikasi ini 
                    adalah sebuah app yang bertujuan untuk memudahkan para Tourist. Di dalam app ini
                    menggunakan sistem seperti GO-JEK dan GRAB atau UBER. Dimana sang Tourist akan 
                    mencari seorang Guider yang sekiranya memumpuni bahasa yang bisa digunakan oleh
                    Tourist tersebut. Jadi App ini menawarkan fitur Chat dan fitur Paketan. Fitur Chat ini
                    lebih banyak digunakan oleh perorangan, seperti Tourist yang backpackeran sendiri. Sistem
                    pembayarannya berdasarkan durasi di chat tersebut. Fitur Paketan ini biasa sering digunakan 
                    oleh Agent Travel yang ingin bekerja sama dengan kita. 
                    </CardText>
                    <CardActions border>
                        <Button colored href="">GitHub</Button>                        
                    </CardActions>
                    <CardMenu className="card-menu">
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
            );
        }
    }
    return (
        <div className="category-tabs">
            <Tabs className={activeTab === '1' ? 'active' : ''} onChange={(tabId) => setActiveTab(tabId)} ripple>
                <Tab>React</Tab>
                <Tab>Laravel</Tab>
                <Tab>SQL</Tab>
                <Tab>C#</Tab>
                <Tab>Swift</Tab>
            </Tabs>

            <Grid className="projects-grid">
                <Cell col={12}>
                    <div className="main-content"><MainCategory/></div>
                </Cell>
            </Grid>
        </div>
        
    );
}
export default project;