import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';
import orangGanteng from '../Picture/orangGanteng.PNG';
import axios from 'axios';

class about extends Component {
    constructor(props){
        super(props);

        this.state = {
            name: '',
            email: '',
            feedback: '',
            redirect: false,
            loading: false,
            message: ""
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e){

      this.setState({
          [e.target.name]: e.target.value
      });
      console.log(this.state);
      
      
  }
  loadShowMsg(){
    if(this.state.loading){
      return <p>Loading...</p>
    }
    else{
      return <p>{this.state.message}</p>
    }
  }

  handleSubmit(e){
      e.preventDefault();

      const name = this.state.name
      const email = this.state.email
      const feedback = this.state.feedback
      

      this.setState ({
        loading:true
      })

      const data = {
          name,
          email,
          feedback
      }
    //   url link bisa diganti sesuai 
      axios.post('/t/kcbti-1554703031/post', data)
      .then(response => {
        console.log(response);
        this.setState({
          loading:false,
          message:response.data,
          redirect:true
        })
      })
      .catch(err => {
        console.log(err);
        this.setState({
          loading:false,
          redirect:false
        });
      });
    }
    render () {
    return (
        <div className="about-body">
            <Grid className="about-grid">
                <Cell col={6}>
                    <h1>Dimas Azis Surahman</h1>
                    <hr/>
                    <img src={orangGanteng} alt="ava" className="fotoGanteng"/>
                    <p>
                        Saya adalah Mahasiswa Binus. Yang dilahirkan di negeri Indonesia, dijaman 
                        dimana semua orang berbondong-bondong, berusaha keras untuk meruntuhkan Soeharto.
                        Yang mana ada sebagian orang, yang berbondong-bondong untuk menjarah barang-barang
                        toko. Dikala itu aku dilahirkan. Bisa dibayangkan betapa sulitnya ibuku mengandungku,
                        mungkin disaat itu ibuku merasa sial telah mengandungku. Yang mana seharusnya ikut
                        menjarah barang-barang di mall, tetapi malah bersusah payah mengandungku. Terima kasih
                        ibuku sudah ikhlas mengandungku di suasana kericuhan tahun 98.
                        Setelah ku lahir baru semua tenang. Mata ku melihat seolah tidak ada yang terjadi.
                        Kupingku mendengar seolah tidak ada yang teriak. Mulutku berteriak bagaikan suara tangisan
                        surga. Disitulah moment saat ku lahir. Yang mana ketika ku berteriak, semua kericuhan 98 pun berakhir.
                        Kalian sudah pasti tahu bulan kapan ku lahir. Disitulah demo 98 berhenti. Semoga peristiwa
                        seperti ini tidak akan terulang kembali. Dan hanya terjadi satu kali saja. Selanjutnya semua 
                        hanya tinggal kenangan semata. Saya Dimas Azis Surahman lahir di tahun kericuhan. Bertumbuh berkembang
                        untuk mengakhiri kericuhan tersebut.
                    </p>
                </Cell>
                <Cell col={6}>
                    <h1>Contact Me</h1>
                    <hr/>
                    <List>
                        <ListItem>
                            <ListItemContent className="contact-phone">
                            <i className="fa fa-phone-square" aria-hidden="true"/>
                            +62 8577 4511 678 (WA)</ListItemContent>
                        </ListItem>
                        <ListItem>
                            <ListItemContent className="contact-phone"><i className="fa fa-envelope" aria-hidden="true"/> dimasazissurahman@gmail.com</ListItemContent>
                        </ListItem>
                        <ListItem>
                            <ListItemContent className="contact-phone"><i className="fa fa-user" aria-hidden="true"/> dimasazis (Line) </ListItemContent>
                        </ListItem>
                    </List>
                    <form className="form-input" onSubmit={this.handleSubmit}>
                    <input className="input-name" id="name" type="text" placeholder="Your Name" name="name" onChange={this.handleChange}/><br/>
                    <input className="input-email" id="email" type="email" placeholder="Your Email" name="email" onChange={this.handleChange}/><br/>
                    <input className="feedback-input" id="feedback" type="text" placeholder="Feedback" name="feedback" onChange={this.handleChange}/>
                    <button className="submit-button" type="submit">Submit</button>
                    </form>
                    {this.loadShowMsg()}
                </Cell>
            </Grid>
        </div>
    );
    }
}

export default about;