import React, { useState } from 'react';
import {Tabs, Tab, Grid, Cell, CardText, Card, CardTitle, CardActions,Button, CardMenu, IconButton } from 'react-mdl';

function gallery () {
    const [activeTab, setActiveTab] = useState(0);
    function MainCategory(){
        if(activeTab === 0){
            return (
            <div className="gallery-main-display">
                <div className="gallery-display">
                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Frustated Quotes "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title1" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" The Choice "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title2" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Respect each moment "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title3" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Be patience "</span>
                    </CardActions>                
                </Card>
                </div>
                <div className="gallery-display1">
                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title4" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Be happy and you're grateful person "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title5" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Respect those who are guests in your heart "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title6" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Said before it changes "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title7" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Be grateful person "</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title8" expand />
                    <CardActions className="gallery-action">
                        <span className="gallery-action-span">" Learn your experiences to make your life better "</span>
                    </CardActions>                
                </Card>
                </div>
            </div> 
            );
        }else if(activeTab === 1){
            return (
            <div className="gallery-wallpaper-display">
                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title-wallpaper" expand />
                    <CardActions className="gallery-action-wallpaper">
                        <span className="gallery-action-span-wallpaper">Motivation Quotes</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title-wallpaper1" expand />
                    <CardActions className="gallery-action-wallpaper">
                        <span className="gallery-action-span-wallpaper">Motivation Quotes</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title-wallpaper2" expand />
                    <CardActions className="gallery-action-wallpaper">
                        <span className="gallery-action-span-wallpaper">Motivation Quotes</span>
                    </CardActions>                
                </Card>

                <Card className="gallery-main" shadow={4}>
                    <CardTitle className="gallery-title-wallpaper3" expand />
                    <CardActions className="gallery-action-wallpaper">
                        <span className="gallery-action-span-wallpaper">Motivation Quotes</span>
                    </CardActions>                
                </Card>
            </div>
            );
        }
    }
    return (
        <div className="gallery-tabs">
            <Tabs className={activeTab === '1' ? 'active' : ''} onChange={(tabId) => setActiveTab(tabId)} ripple>
                <Tab>Quotes</Tab>
                <Tab>Photo</Tab>                
            </Tabs>

            <Grid className="projects-grid">
                <Cell col={12}>
                    <div className="main-content"><MainCategory/></div>
                </Cell>
            </Grid>
        </div>
    );

}

export default gallery;